.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 63px;
  .title {
    font-weight: 600;
    font-size: 26px;
    line-height: 121.2%;
    text-align: center;
    padding: 0 20px 15px 20px;
    color: #222427;
  }
  .text {
    font-size: 15px;
    line-height: 22px;
    padding: 0 20px 0 20px;
    text-align: center;
    letter-spacing: 0.273195px;

    color: #222427;

    opacity: 0.95;
  }
  .animation {
    width: 250vw;
    height: 450px;

    overflow: hidden;
    position: relative;
    &Block {
      position: absolute;
      left: -50%;
      background: #15c963;
      border-radius: 20px;
      animation: move 10s linear 2s infinite,
        jiggle 2.1s cubic-bezier(0.37, 0, 0.63, 1) infinite;
    }
    // &::after {
    //   position: absolute;
    //   content: "";
    //   height: 100%;
    //   width: 100%;
    //   top: 0;
    //   left: 0;
    //   background: radial-gradient(
    //     circle at center,
    //     hsla(0, 0%, 100%, 0) 20%,
    //     #fff 75%
    //   );
    // }
  }

  .emojiText {
    display: flex;
    flex-direction: row;
    padding: 12px;
    .emoji {
      height: 21px;
    }
    .blockText {
      font-weight: 500;
      font-size: 15px;
      line-height: 22px;

      letter-spacing: 0.273195px;

      color: #ffffff;

      flex: none;
      order: 1;
      flex-grow: 0;
      margin: 0px 10px;
    }
  }
}

@keyframes move {
  0% {
    left: 0;
  }
  100% {
    left: 100%;
  }
}

@keyframes jiggle {
  0% {
    transform: translateY(-8px);
  }

  50% {
    transform: translateY(8px);
  }

  to {
    transform: translateY(-8px);
  }
}
